import { Box, Button, Grid } from '@material-ui/core';
import { format, parseISO } from 'date-fns';
import { it } from 'date-fns/locale';
import { get } from 'lodash';
import PropTypes from 'prop-types';
import React, { useState } from 'react';
import { Trans, useTranslation } from 'react-i18next';
import ActionManager from '../../context/action/ActionManager';
import DialogManager from '../../context/dialog/DialogManager';
import UserManager from '../../context/user/UserManager';
import { ROLES } from '../../helpers/utils';
import { OPTIONS_STATE } from '../__types__/types';

import BoxInfo from '../BoxInfo/BoxInfo';
import BoxInfoWithAvatar from '../BoxInfo/BoxInfoWithAvatar';
import InputInfoWithPopper from '../Input/InputInfoWithPopper';
import InputSelect from '../Input/InputSelect';
import detailActionInfo from './style/detailActionStyle';
import DialogConfirm from '../Dialog/Confirm/DialogConfirm';
import CheckIcon from '@material-ui/icons/Check';
import CommentRoundedIcon from '@material-ui/icons/CommentRounded';
import DialogComment from '../Dialog/Comment/DialogComment';

const DetailActionInfo = ({ type, action, refId }) => {
  const { logged, users } = UserManager();
  const {
    updateSubActionStatus,
    updateActionStatus,
    updateActionAndSubactionAck,
    updateActionAndSubactionComment
  } = ActionManager();

  const dialogManager = DialogManager();
  const { t } = useTranslation();

  const [isOpenConfirmAckDialog, setIsOpenConfirmAckDialog] = useState(false);
  const [isOpenCommentSendDialog, setOpenCommentSendDialog] = useState(false);

  const handleSendCommentDialog = () => {
    setOpenCommentSendDialog(prev => !prev);
  };

  const [sendAckState, setAckState] = useState({
    value: null,
  });

  const handleOnClickSendAck = () => {
    setAckState({ value: true });
    setIsOpenConfirmAckDialog(true);
  };

  const onCancelSendAckHandler = () => {
    setAckState({ value: null });
    setIsOpenConfirmAckDialog(false);
  };

  const onConfirmSendAckHandler = () => {
    if (sendAckState.value) {
      updateActionAndSubactionAck(action.id, type);
    }
    setIsOpenConfirmAckDialog(false);
    setAckState({ value: null });
  };

  // eslint-disable-next-line react/prop-types
  const { id, code, title, endDate, description, canComplete, status } = action;

  const css = detailActionInfo();

  const openDialog = (role, user) => {
    dialogManager.setAllState({
      type,
      openDialog: true,
      userSelected: user,
      userRole: role,
    });
  };

  const showBoxWithAvatar = () => {
    function pendingActionTranslator(item) {
      if (item.pendingAck && item.pendingComment) {
        return 'both';
      }
      if (item.pendingAck) {
        return 'ack';
      }
      if (item.pendingComment) {
        return 'comment';
      }

      return undefined;
    }

    if (users) {
      return Array.from(ROLES).map(raciRole => (
        <Grid item xs={12} key={raciRole}>
          <BoxInfoWithAvatar
            title={t(`entities.info.${raciRole}`)}
            name={
              users[action[raciRole]]
                ? `${users[action[raciRole]]?.firstname} ${users[action[raciRole]]?.lastname}`
                : ''
            }
            role={get(users[`${action[raciRole]}`], 'role', t('entities.info.empty'))}
            avatar={`${action[raciRole]}.jpg`}
            cta={() => openDialog(raciRole, users[action[raciRole]])}
            raciRole={raciRole}
            pendingActions={pendingActionTranslator(action)}
          />
        </Grid>
      ));
    }
    return null;
  };

  const handleChangeStatus = newStatus => {
    if (type === 'action') {
      if (status !== newStatus) {
        updateActionStatus(id, newStatus);
      }
    } else {
      updateSubActionStatus(id, newStatus);
    }
  };

  const handleOnClickSendComment = () => {
    handleSendCommentDialog();
  };

  const addComment = comment => {
    updateActionAndSubactionComment(type,id,comment);
  };

  const getCommentDialogLabel = () => {
   if (type === 'action') {
     return 'entities.action.label'
   }else {
     return 'entities.subaction.label'
   }
  };

  return (
    <>
      {isOpenConfirmAckDialog ? (
        <DialogConfirm
          open={isOpenConfirmAckDialog}
          message={t(
            type === 'action' ? 'entities.action.sendAckMsg' : 'entities.subaction.sendAckMsg'
          )}
          onCancel={onCancelSendAckHandler}
          onConfirm={onConfirmSendAckHandler}
        />
      ) : null}

      <Grid container spacing={2} className={css.gridContainer}>
        <Grid item xs={12}>
          <BoxInfo
            title={`${
              type === 'action' ? t('entities.action.action') : t('entities.action.subaction')
            } - ${refId}-${code}`}
            content={title}
          />
        </Grid>
        {type !== 'action' ? (
          <Grid item xs={12}>
            <BoxInfo title={t('entities.action.detail.description')} content={description} />
          </Grid>
        ) : null}

        <Grid item xs={12}>
          <Grid container spacing={3}>
            <Grid item xs={6}>
              <Box>
                <BoxInfo
                  title={t('entities.action.within')}
                  content={endDate && format(parseISO(endDate), 'dd MMMM yyyy', { locale: it })}
                  textTransform="capitalize"
                />
              </Box>
            </Grid>
            <Grid item xs={6}>
              <Box className={css.boxState}>
                <InputInfoWithPopper
                  title={t('entities.action.state')}
                  contentPopper={{
                    title: (
                      <Trans t={t} i18nKey="entities.action.popper.title">
                        {[<span key={0} className={css.highlight} />]}
                      </Trans>
                    ),
                    description: t('entities.action.popper.description'),
                  }}
                >
                  <InputSelect
                    name="status"
                    options={OPTIONS_STATE}
                    initialValue={status || ''}
                    emptyOption={t('entities.action.noStatus')}
                    highlightKey={OPTIONS_STATE[0].id}
                    disabledKey={canComplete === false ? OPTIONS_STATE[1].id : null}
                    onChange={handleChangeStatus}
                  />
                </InputInfoWithPopper>
              </Box>
            </Grid>
          </Grid>
        </Grid>

        {type === 'action' ? (
          <Grid item xs={12}>
            <BoxInfo title={t('entities.action.detail.description')} content={description} />
          </Grid>
        ) : null}

        {type === 'subAction' && (action.pendingAck === true || action.pendingComment === true) ? (
          <Grid item xs={12}>
            <Box className={css.boxInfoSubActionPendingAction}>
              <InputInfoWithPopper
                title={t('entities.subaction.subActionActionPending')}
                contentPopper={{
                  title: '',
                  description: t('entities.subaction.popper.pendingAction'),
                }}
                icon="left"
              />
            </Box>
          </Grid>
        ) : (
          ''
        )}

        {logged &&
        action.informed &&
        logged.toUpperCase() === action.informed.toUpperCase() &&
        action.pendingAck ? (
          <Grid item xs={12}>
            <Button
              variant="contained"
              size="large"
              className={css.buttonSendAck}
              onClick={handleOnClickSendAck}
              endIcon={<CheckIcon />}
              title={t('button.ack.title')}
            >
              {t('button.ack.content')}
            </Button>
          </Grid>
        ) : null}

        {logged &&
        action.consulted &&
        logged.toUpperCase() === action.consulted.toUpperCase() &&
        action.pendingComment ? (
          <Grid item xs={12}>
            <Button
              variant="contained"
              size="large"
              className={css.buttonSendAck}
              onClick={handleOnClickSendComment}
              endIcon={<CommentRoundedIcon />}
              title={t('button.comment.title')}
            >
              {t('button.comment.content')}
            </Button>
            <DialogComment open={isOpenCommentSendDialog} handleClose={handleSendCommentDialog} handleAddComment={addComment} labelKey={getCommentDialogLabel()} targetId={`${refId}-${code}`}/>
          </Grid>
        ) : null}
        {showBoxWithAvatar()}
      </Grid>
    </>
  );
};

DetailActionInfo.propTypes = {
  action: PropTypes.shape({}).isRequired,
  refId: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired,
  type: PropTypes.oneOf(['action', 'subAction']).isRequired,
};

export default DetailActionInfo;
