import { Avatar, Box, IconButton, Typography } from '@material-ui/core';
import CreateIcon from '@material-ui/icons/Create';
import PropTypes from 'prop-types';
import React from 'react';
import TaskManager from '../../context/task/TaskManager';
import boxInfoWithAvatar from './style/boxInfoWithAvatarStyle';
import PendingActionAlert from '../PendingAction/PendingActionAlert';
import { CONSULTED, INFORMED, ROLES } from '../../helpers/utils';
import CheckIcon from '@material-ui/icons/Check';
import CommentRoundedIcon from '@material-ui/icons/CommentRounded';
import ReactTooltip from 'react-tooltip';
import { randomLabelGenerator } from '../common/GeneralLibraries';
import { useTranslation } from 'react-i18next';


const BoxInfoWithAvatar = ({ avatar, title, name, role, cta, pendingActions, raciRole }) => {

  const { t } = useTranslation();
  const { metadata } = TaskManager();

  const pendingActionAdviceAck = (
    <PendingActionAlert popupTextKey="message.pendingAck" positionPlacement="right" />
  );

  const pendingActionAdviceComment = (
    <PendingActionAlert popupTextKey="message.pendingComment" positionPlacement="right" />
  );

  function userAckedIcon() {
    let label = randomLabelGenerator();
    return (
      <span>
        <CheckIcon htmlColor="#4f8b46" data-tip data-for={label}/>
        <ReactTooltip id={label} type="success" effect="solid" place="left" backgroundColor="#4f8b46">
          <span>{t('tooltip.ackPresent')}</span>
        </ReactTooltip>
      </span>
    );
  }

  function userCommentedIcon() {
    let label = randomLabelGenerator();

    return (
      <span>
        <CommentRoundedIcon htmlColor="#4f8b46" data-tip data-for={label}/>
        <ReactTooltip id={label} type="success" effect="solid" place="left" backgroundColor="#4f8b46">
          <span>{t('tooltip.commentPresent')}</span>
        </ReactTooltip>
      </span>
    );
  }

  const css = boxInfoWithAvatar();

  function pendingActionAdviceCheck() {
    if (name !== '' && pendingActions) {
      if (raciRole === CONSULTED && (pendingActions === 'both' || pendingActions === 'comment')) {
        return pendingActionAdviceComment;
      }
      if (raciRole === INFORMED && (pendingActions === 'both' || pendingActions === 'ack')) {
        return pendingActionAdviceAck;
      }
    }
  }

  function userHasAcked() {

    if (name !== '' && (raciRole === INFORMED)) {
      if (!pendingActions || pendingActions === 'comment') {
        return userAckedIcon();
      }
    }
    return ''
  }
  function userHasCommented() {

    if (name !== '' && (raciRole === CONSULTED)) {
      if (!pendingActions || pendingActions === 'ack') {
        return userCommentedIcon();
      }
    }
    return ''
  }


  return (
    <>
      <Box textAlign="left">
        <Typography variant="body1" color="textSecondary">
          {title} {pendingActionAdviceCheck()}
        </Typography>
      </Box>
      <Box
        className={css.borderBox}
        textAlign="left"
        p={2}
        display="flex"
        flexDirection="row"
        flexWrap="nowrap"
        justifyContent="flex-start"
        alignItems="center"
        alignContent="flex-start"
      >
        <Box display="flex" flexWrap="nowrap" alignItems="center" width={1}>
          <Box p={1} marginRight={2}>
            <Avatar alt={name} src={avatar} />
          </Box>
          <Box>
            <Typography variant="body1" color="textPrimary">
              {name}
            </Typography>
            <Typography variant="body2" color="textSecondary">
              {role}
            </Typography>
          </Box>
        </Box>
        {userHasAcked()}
        {userHasCommented()}
        {metadata.canChangeTaskRoles ? (
          <Box>
            <IconButton data-testid={title} aria-label="change" onClick={cta} disableRipple>
              <CreateIcon />
            </IconButton>
          </Box>
        ) : null}
      </Box>
    </>
  );
};

BoxInfoWithAvatar.propTypes = {
  title: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  role: PropTypes.string.isRequired,
  avatar: PropTypes.string.isRequired,
  cta: PropTypes.func,
  pendingActions: PropTypes.oneOf(['ack', 'comment', 'both']),
  raciRole: PropTypes.oneOf(ROLES),
};

BoxInfoWithAvatar.defaultProps = {
  cta: () => {},
};

export default BoxInfoWithAvatar;
