import { Box, Button, Grid } from '@material-ui/core';
import DialogConfirm from 'components/Dialog/Confirm/DialogConfirm';
import InputDatePicker from 'components/Input/InputDataPicker';
import InputInfo from 'components/Input/InputInfo';
import { get } from 'lodash';
import React, { useState } from 'react';
import { Trans, useTranslation } from 'react-i18next';
import DialogManager from '../../context/dialog/DialogManager';
import TaskManager from '../../context/task/TaskManager';
import UserManager from '../../context/user/UserManager';
import { ROLES } from '../../helpers/utils';
import BoxInfo from '../BoxInfo/BoxInfo';
import BoxInfoWithAvatar from '../BoxInfo/BoxInfoWithAvatar';
import InputInfoWithPopper from '../Input/InputInfoWithPopper';
import InputSelect from '../Input/InputSelect';
import { OPTIONS_TASK_STATE } from '../__types__/types';
import taskInfoStyle from './style/taskInfoStyle';
import CheckIcon from '@material-ui/icons/Check';
import CommentRoundedIcon from '@material-ui/icons/CommentRounded';
import DialogComment from '../Dialog/Comment/DialogComment';

const TaskInfo = () => {
  const { task, metadata, updateTaskStatus, updateTaskDates, updateAckTaskStatus, updateTaskComment } = TaskManager();
  const { setAllState } = DialogManager();
  const { logged, users } = UserManager();
  const { t } = useTranslation();
  const css = taskInfoStyle();

  const { status, canComplete, id } = task;

  const [isOpenConfirmDialog, setIsOpenConfirmDialog] = useState(false);
  const [isOpenConfirmAckDialog, setIsOpenConfirmAckDialog] = useState(false);
  const [isOpenCommentSendDialog, setOpenCommentSendDialog] = useState(false);

  const handleSendCommentDialog = () => {
    setOpenCommentSendDialog(prev => !prev);
  };

  const [updateDatesState, setUpdateDatesState] = useState({
    field: '',
    value: null,
  });

  const [sendAckState, setAckState] = useState({
    value: null,
  });

  const openDialog = (userRole, userSelected) => {
    setAllState({
      type: 'task',
      openDialog: true,
      userSelected,
      userRole,
    });
  };

  function pendingActionTranslator(taskItem) {
    if (taskItem.pendingAck && taskItem.pendingComment) {
      return 'both';
    }
    if (taskItem.pendingAck) {
      return 'ack';
    }
    if (taskItem.pendingComment) {
      return 'comment';
    }
    return undefined;
  }

  const showBoxWithAvatar = () => {
    if (users) {
      return Array.from(ROLES).map(raciRole => (
        <Grid item xs={12} key={raciRole}>
          <BoxInfoWithAvatar
            title={t(`entities.info.${raciRole}`)}
            name={
              users[task[raciRole]]
                ? `${users[task[raciRole]]?.firstname} ${users[task[raciRole]]?.lastname}`
                : ''
            }
            role={get(users[`${task[raciRole]}`], 'role', t('entities.info.empty'))}
            avatar={`${task[raciRole]}.jpg`}
            cta={() => openDialog(raciRole, users[task[raciRole]])}
            raciRole={raciRole}
            pendingActions={pendingActionTranslator(task)}
          />
        </Grid>
      ));
    }
    return null;
  };

  const handleChangeStatus = newStatus => {
    if (status !== newStatus) {
      updateTaskStatus(id, newStatus);
    }
  };

  const onChangeDatesHandler = (updatingDateType, newDate) => {
    if (updatingDateType === 'startDate') {
      if (newDate && newDate !== task.startDate) {
        setIsOpenConfirmDialog(true);
        setUpdateDatesState({ field: 'startDate', value: newDate });
      }
    }
    if (updatingDateType === 'endDate') {
      if (newDate && newDate !== task.endDate) {
        setUpdateDatesState({ field: 'endDate', value: newDate });
        setIsOpenConfirmDialog(true);
      }
    }
  };

  const handleOnClickSendComment = () => {
    handleSendCommentDialog();
  };

  const handleOnClickSendAck = () => {
    setAckState({ value: true });
    setIsOpenConfirmAckDialog(true);
  };

  const onCancelChangeDatesHandler = () => {
    setUpdateDatesState({ field: '', value: null });
    setIsOpenConfirmDialog(false);
  };

  const onConfirmChangeDatesHandler = () => {
    updateTaskDates(id, updateDatesState.field, updateDatesState.value);
    setIsOpenConfirmDialog(false);
  };

  const onCancelSendAckHandler = () => {
    setAckState({ value: null });
    setIsOpenConfirmAckDialog(false);
  };

  const onConfirmSendAckHandler = () => {
    if (sendAckState.value) {
      updateAckTaskStatus(id);
    }
    setIsOpenConfirmAckDialog(false);
    setAckState({ value: null });
  };

  const addComment = comment => {
    updateTaskComment(task.id,comment);
  };


  return (
    <>
      {isOpenConfirmDialog ? (
        <DialogConfirm
          open={isOpenConfirmDialog}
          message={t('entities.task.changeDatesMsg')}
          onCancel={onCancelChangeDatesHandler}
          onConfirm={onConfirmChangeDatesHandler}
        />
      ) : null}

      {isOpenConfirmAckDialog ? (
        <DialogConfirm
          open={isOpenConfirmAckDialog}
          message={t('entities.task.sendAckMsg')}
          onCancel={onCancelSendAckHandler}
          onConfirm={onConfirmSendAckHandler}
        />
      ) : null}

      <Grid container spacing={2} className={css.gridContainer}>
        <Grid item xs={6}>
          <BoxInfo title={t('entities.info.id')} content={task.id} />
        </Grid>
        <Grid item xs={6}>
          <Box className={css.boxState}>
            <InputInfoWithPopper
              title={t('entities.action.state')}
              contentPopper={{
                title: (
                  <Trans t={t} i18nKey="entities.action.popper.title">
                    {[<span key={0} className={css.highlight} />]}
                  </Trans>
                ),
                description: t('entities.action.popper.description'),
              }}
            >
              <InputSelect
                name="status"
                options={OPTIONS_TASK_STATE}
                initialValue={status || ''}
                emptyOption={t('entities.action.noStatus')}
                disabled={canComplete === false}
                onChange={handleChangeStatus}
              />
            </InputInfoWithPopper>
          </Box>
        </Grid>

        <Grid item xs={12}>
          <BoxInfo title={t('entities.info.task')} content={task.title} />
        </Grid>
        <Grid item xs={12}>
          <Grid container spacing={3}>
            <Grid item xs={6}>
              <Box>
                <InputInfo title={t('entities.info.startDate')}>
                  <Box border={0}>
                    <InputDatePicker
                      name="startDate"
                      format="dd MMMM yyyy"
                      rules={{
                        required: {
                          value: true,
                          message: t('validation.required'),
                        },
                      }}
                      initialValue={task.startDate}
                      onChange={newDate => onChangeDatesHandler('startDate', newDate)}
                      maxDate={task.endDate}
                      disabled={!metadata.canChangeTaskDates}
                    />
                  </Box>
                </InputInfo>
              </Box>
            </Grid>
            <Grid item xs={6}>
              <Box>
                <InputInfo title={t('entities.info.endDate')}>
                  <Box border={0}>
                    <InputDatePicker
                      name="endDate"
                      format="dd MMMM yyyy"
                      rules={{
                        required: {
                          value: true,
                          message: t('validation.required'),
                        },
                      }}
                      initialValue={task.endDate}
                      onChange={newDate => onChangeDatesHandler('endDate', newDate)}
                      minDate={task.startDate}
                      disabled={!metadata.canChangeTaskDates}
                    />
                  </Box>
                </InputInfo>
              </Box>
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={12}>
          <BoxInfo title={t('entities.info.description')} content={task.description} />
        </Grid>
        {logged &&
        task.informed &&
        logged.toUpperCase() === task.informed.toUpperCase() &&
        task.pendingAck ? (
          <Grid item xs={12}>
            <Button
              variant="contained"
              size="large"
              className={css.buttonSendAck}
              onClick={handleOnClickSendAck}
              endIcon={<CheckIcon />}
              title={t('button.ack.title')}
            >
              {t('button.ack.content')}
            </Button>
          </Grid>
        ) : null}

        {logged &&
        task.consulted &&
        logged.toUpperCase() === task.consulted.toUpperCase() &&
        task.pendingComment ? (
          <Grid item xs={12}>
            <Button
              variant="contained"
              size="large"
              className={css.buttonSendAck}
              onClick={handleOnClickSendComment}
              endIcon={<CommentRoundedIcon />}
              title={t('button.comment.title')}
            >
              {t('button.comment.content')}
            </Button>
            <DialogComment open={isOpenCommentSendDialog} handleClose={handleSendCommentDialog} handleAddComment={addComment} labelKey="entities.task.label" targetId={task.id}/>
          </Grid>
        ) : null}
        {showBoxWithAvatar()}
      </Grid>
    </>
  );
};

export default TaskInfo;
